
.side-characters-container {
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
  max-width: 100%;
}

/* 次要角色列表 */
.side-character-list {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
  gap: 1.5rem;
}

/* 空状态样式 */
.empty-state-card {
  background: rgba(139, 170, 199, 0.08);
  border: 1px solid var(--glass-border);
  border-radius: 16px;
  padding: 2.5rem 2rem;
  text-align: center;
  backdrop-filter: blur(8px);
  position: relative;
  overflow: hidden;

  &::before {
    content: '';
    position: absolute;
    top: 0;
    left: -100%;
    width: 100%;
    height: 100%;
    background: linear-gradient(90deg, transparent, rgba(139, 170, 199, 0.1), transparent);
    transition: left 0.6s ease;
  }

  &:hover::before {
    left: 100%;
  }
}

.empty-state-content {
  position: relative;
  z-index: 2;
}

.empty-state-icon {
  width: 64px;
  height: 64px;
  margin: 0 auto 1.5rem;
  opacity: 0.6;

  .icon {
    width: 100%;
    height: 100%;
    color: var(--accent-purple);
  }
}

.empty-state-title {
  font-size: 1.5rem;
  font-weight: 700;
  color: var(--text-primary);
  margin-bottom: 1rem;
  font-family: 'Great Vibes', cursive;
}

.empty-state-description {
  font-size: 1rem;
  color: var(--text-secondary);
  line-height: 1.6;
  margin-bottom: 2rem;
  opacity: 0.8;
}

.empty-state-footer {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.5rem;
  font-size: 0.9rem;
  color: var(--text-muted);
  font-style: italic;

  svg {
    opacity: 0.7;
  }
}

/* 响应式设计 */
@media (max-width: 768px) {
  .side-character-list {
    grid-template-columns: 1fr;
    gap: 1rem;
  }

  .empty-state-card {
    padding: 2rem 1.5rem;
  }

  .empty-state-title {
    font-size: 1.3rem;
  }

  .empty-state-description {
    font-size: 0.9rem;
  }
}

@media (max-width: 480px) {
  .side-characters-container {
    gap: 1rem;
  }

  .empty-state-card {
    padding: 1.5rem 1rem;
  }

  .empty-state-icon {
    width: 48px;
    height: 48px;
    margin-bottom: 1rem;
  }

  .empty-state-title {
    font-size: 1.2rem;
  }

  .empty-state-description {
    font-size: 0.85rem;
    margin-bottom: 1.5rem;
  }

  .empty-state-footer {
    font-size: 0.8rem;
  }
}
